@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

@import './variables.scss';

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Lato', sans-serif;
}

table,
th,
td {
	padding: 0;
	margin: 0;
}

.App {
	width: 100%;
	height: 100dvh;
	display: grid;
	place-content: center;
}
